/*
 * Global vars app
 **/
$font-size: 11px;
$font-family: Verdana, Arial, sans-serif;

/**
 * Forms vars
 */
$form-ctrl-size: 11px;
$form-ctrl-height: 22px;
$form-ctrl-border-color: #66afe9;

/*
 * Badge vars
 */
$badge-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
$badge-padding: 2px 9px;
$badge-default-color: #999999;
$badge-success-color: #56bc76;
$badge-primary-color: #618fb0;
$badge-info-color: #4ab0ce;
$badge-warning-color: #eac85e;
$badge-danger-color: #e5603b;
$state-danger-text: #a94442;



